<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-button type="primary" class="mg-b" @click="addCategories"></el-button>
      <!--  -->
      <el-table
        :data="categoriesList"
        border
        stripe
        style="100%"
        row-key="cat_id"
        :tree-props="{children: 'children'}"
      >
        <el-table-column type="index" label="#" align="center">
          <template v-slot="slotsProps">
            <i class="el-icon-s-help" v-if="slotsProps.row.cat_level==0"></i>
          </template>
        </el-table-column>
        <el-table-column label="" prop="cat_name"></el-table-column>
        <el-table-column label="">
          <template v-slot="slotsProps">
            <i
              class="el-icon-success"
              style="color: lightgreen"
              v-if="slotsProps.row.cat_deleted===false"
            ></i>
            <i class="el-icon-error" style="color: red" v-else></i>
          </template>
        </el-table-column>
        <el-table-column label="" sortable>
          <template v-slot="slotsProps">
            <el-tag type="primary" v-if="slotsProps.row.cat_level==0"></el-tag>
            <el-tag type="success" v-if="slotsProps.row.cat_level==1"></el-tag>
            <el-tag type="warning" v-if="slotsProps.row.cat_level==2"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template v-slot="slotsProps">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="editCategories(slotsProps.row)"
            ></el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="deleteCategories(slotsProps.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="sizeChange"
        @current-change="currentChange"
        :current-page="reqParams.pagenum"
        :page-sizes="[5, 10, 20]"
        :page-size="reqParams.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>

      <!--  -->
      <div>
        <!--  -->
        <el-dialog title="" :visible.sync="addCateVisible" width="50%" @close="resetAddCate">
          <el-form
            :model="addCateForm"
            :rules="addCateFormRules"
            ref="addCateFormRef"
            label-width="80px"
          >
            <el-form-item label="" prop="cat_name">
              <el-input v-model="addCateForm.cat_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="cat_level">
              <el-cascader
                v-model="selectdKeys"
                :props="cateProps"
                :options="parentCateList"
                @change="handleChange"
                clearable
                filterable
              ></el-cascader>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="addCateVisible = false"> </el-button>
            <el-button type="primary" @click="addParentCate"> </el-button>
          </span>
        </el-dialog>
        <!--  -->
        <el-dialog
          title=""
          :visible.sync="editCateVisible"
          width="50%"
          @close="resetEditVisible"
        >
          <el-form
            :model="editCateForm"
            :rules="editCateFormRules"
            ref="editCateFormRef"
            label-width="80px"
          >
            <el-form-item label="" prop="cat_name">
              <el-input v-model="editCateForm.cat_name"></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="editCateVisible = false"> </el-button>
            <el-button type="primary" @click="editCate"> </el-button>
          </span>
        </el-dialog>
      </div>
    </el-card>
  </div>
</template>

<script>
import qs from 'qs'

export default {
  data() {
    return {
      categoriesList: [],
      // 
      reqParams: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      total: 0,
      // 
      addCateVisible: false,
      // 
      addCateForm: {
        cat_pid: 0,
        cat_name: '',
        cat_level: 0
      },
      // cat_name
      addCateFormRules: {
        cat_name: [
          { required: true, message: '', trriger: 'blur' },
          { max: 20, message: '20', trriger: 'blur' }
        ]
      },
      // 
      parentCateList: [],
      // props
      cateProps: {
        expandTrigger: 'hover',
        checkStrictly: true,
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // id
      selectdKeys: [],
      // 
      editCateVisible: false,
      // 
      editCateForm: {},
      // 
      editCateFormRules: {
        cat_name: [
          { required: true, message: '', trriger: 'blur' },
          { max: 20, message: '20', trriger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.getCategoriesList()
  },
  methods: {
    // 
    async getCategoriesList() {
      const { data: result } = await this.$http.get('/categories', {
        params: this.reqParams
      })
      if (result.meta.status !== 200) {
        return this.$message.error('')
      }
      this.categoriesList = result.data.result
      this.total = result.data.total
    },
    // 
    sizeChange(newSize) {
      this.reqParams.pagesize = newSize
      this.reqParams.pagenum = 1
      this.getCategoriesList()
    },
    // 
    currentChange(newPage) {
      this.reqParams.pagenum = newPage
      this.getCategoriesList()
    },
    // 
    addCategories() {
      this.cascaderCateList()
      this.addCateVisible = true
    },
    // 
    resetAddCate() {
      this.$refs.addCateFormRef.resetFields()
      this.selectdKeys = []
      this.addCateForm.cat_level = 0
      this.addCateForm.cat_pid = 0
    },
    // 
    async cascaderCateList() {
      const { data: result } = await this.$http.get('categories', {
        params: { type: 2 }
      })
      if (result.meta.status !== 200) {
        return this.$message.error('')
      } else {
        this.parentCateList = result.data
      }
    },
    // 
    handleChange() {
      if (this.selectdKeys.length > 0) {
        this.addCateForm.cat_pid = this.selectdKeys[this.selectdKeys.length - 1]
        this.addCateForm.cat_level = this.selectdKeys.length
      } else {
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_level = 0
      }
    },
    // 
    addParentCate() {
      this.$refs.addCateFormRef.validate(async valid => {
        if (!valid) return
        const { data: result } = await this.$http.post(
          '/categories',
          qs.stringify(this.addCateForm),
          { headers: { 'Content-Type': 'application/x-www-form-urlencoded' } }
        )
        if (result.meta.status !== 201) {
          return this.$message.error('')
        } else {
          this.$message.success('')
          this.getCategoriesList()
          this.addCateForm.cat_name = ''
          this.addCateVisible = false
        }
      })
    },
    // 
    editCategories(cn) {
      this.editCateForm = cn
      this.editCateVisible = true
    },
    // 
    resetEditVisible() {
      this.$refs.editCateFormRef.resetFields()
      this.getCategoriesList()
    },
    // 
    editCate() {
      this.$refs.editCateFormRef.validate(async valid => {
        if (!valid) return
        const {
          data: result
        } = await this.$http.put(
          `/categories/${this.editCateForm.cat_id}`,
          qs.stringify({ cat_name: this.editCateForm.cat_name }),
          { headers: { 'Content-Type': 'application/x-www-form-urlencoded' } }
        )
        if (result.meta.status !== 200) {
          return this.$message.error('')
        } else {
          this.$message.success('')
          this.getCategoriesList()
          this.editCateVisible = false
          this.$refs.editCateFormRef.resetFields()
        }
      })
    },
    // 
    async deleteCategories(cid) {
      const confirm = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirm === 'confirm') {
        const { data: result } = await this.$http.delete(
          `/categories/${cid.cat_id}`
        )
        if (result.meta.status !== 200) {
          return this.$message.error('')
        } else {
          this.$message.success('')
          this.getCategoriesList()
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.mg-b {
  margin-bottom: 15px;
}
.el-cascader {
  width: 100%;
}
</style>