<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-alert title="" type="warning" show-icon :closable="false" class="mg-b"></el-alert>
      <!--  -->
      <div class="mg-b">
        
        <el-cascader
          v-model="selectedCate"
          :options="categoriesList"
          :props="receiveProps"
          @change="handleChange"
          clearable
        ></el-cascader>
      </div>
      <!--  -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!--  -->
        <el-tab-pane label="" name="many">
          <el-button
            type="primary"
            size="small"
            class="mg-b"
            :disabled="isBtnDisabled"
            @click="showAddVisible"
          ></el-button>
          <el-table :data="cateParams" style="width: 100%" border stripe>
            <el-table-column type="expand">
              <template v-slot="slotsProps">
                <!--  -->
                <el-tag
                  v-for="(item, i) in slotsProps.row.attr_vals"
                  :key="i"
                  closable
                  @close="handleClose(slotsProps.row)"
                >{{item}}</el-tag>
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="slotsProps.row.inputVisible"
                  v-model="slotsProps.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(slotsProps.row)"
                  @blur="handleInputConfirm(slotsProps.row)"
                ></el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(slotsProps.row)"
                >+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column prop="attr_name" label=""></el-table-column>
            <el-table-column label="">
              <template v-slot="slotsProps">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-edit"
                  @click="showEditPramsDialog(slotsProps.row)"
                ></el-button>
                <el-button
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  @click="delParams(slotsProps.row)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!--  -->
        <el-tab-pane label="" name="only">
          <el-button
            type="primary"
            size="small"
            class="mg-b"
            :disabled="isBtnDisabled"
            @click="showAddVisible"
          ></el-button>
          <el-table :data="cateParams" style="width: 100%" border stripe>
            <el-table-column type="expand">
              <template v-slot="slotsProps">
                <!--  -->
                <el-tag
                  v-for="(item, i) in slotsProps.row.attr_vals"
                  :key="i"
                  closable
                  @close="handleClose(slotsProps.row)"
                >{{item}}</el-tag>
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="slotsProps.row.inputVisible"
                  v-model="slotsProps.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(slotsProps.row)"
                  @blur="handleInputConfirm(slotsProps.row)"
                ></el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(slotsProps.row)"
                >+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column prop="attr_name" label=""></el-table-column>
            <el-table-column label="">
              <template v-slot="slotsProps">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-edit"
                  @click="showEditPramsDialog(slotsProps.row)"
                ></el-button>
                <el-button
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  @click="delParams(slotsProps.row)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!--  -->
    <div>
      <!-- / -->
      <el-dialog
        :title="'' + addParamsText"
        :visible.sync="addVisible"
        @close="closeAddDialog"
        width="50%"
      >
        <!--  -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
          <el-form-item :label="addParamsText" prop="attr_name">
            <el-input v-model="addForm.attr_name"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addVisible = false"> </el-button>
          <el-button type="primary" @click="addAttr"> </el-button>
        </span>
      </el-dialog>

      <!-- / -->
      <el-dialog
        :title="'' + addParamsText"
        :visible.sync="editParamsVisible"
        width="50%"
        @close="resetEditParamsForm"
      >
        <!--  -->
        <el-form
          :model="editParamsForm"
          :rules="editParamsFormRules"
          ref="editParamsFormRef"
          label-width="80px"
        >
          <el-form-item label="" prop="attr_name">
            <el-input v-model="editParamsForm.attr_name"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="editParamsVisible = false"> </el-button>
          <el-button type="primary" @click="editParams()"> </el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import qs from 'qs'

export default {
  data() {
    return {
      // 
      categoriesList: [],
      // 
      receiveProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 
      selectedCate: [],
      // 
      activeName: 'many',
      cateParams: [],
      // /
      addVisible: false,
      addForm: {
        attr_name: ''
      },
      addFormRules: {
        attr_name: [
          {
            required: true,
            message: '',
            trriger: 'blur'
          },
          {
            max: 20,
            message: '20',
            trriger: 'blur'
          }
        ]
      },
      // /
      editParamsVisible: false,
      // /
      editParamsForm: {},
      // /
      editParamsFormRules: {
        attr_name: [
          { required: true, message: '', trriger: 'blur' },
          { max: 20, message: '20', trriger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.getCategoriesList()
  },
  methods: {
    // 
    async getCategoriesList() {
      const { data: result } = await this.$http.get('/categories')
      if (result.meta.status !== 200) {
        return this.$message.error('')
      } else {
        this.categoriesList = result.data
      }
    },
    // 
    async getCateParams() {
      const { data: result } = await this.$http.get(
        `/categories/${this.categoriesID}/attributes`,
        {
          params: { sel: this.activeName }
        }
      )
      if (result.meta.status !== 200) {
        return this.$message.error('')
      } else {
        // 1. ('')tag
        result.data.forEach(item => {
          // 2. ''[""]attr_vals
          item.attr_vals ? (item.attr_vals = item.attr_vals.split(' ')) : ''
          // 3.  inputVisible  inputValue  +new tags 
          item.inputVisible = false
          item.inputValue = ''
        })
        this.cateParams = result.data
      }
    },
    // 
    handleChange() {
      if (this.selectedCate.length === 0) {
        this.cateParams = []
        return
      }
      this.getCateParams()
    },
    // 
    handleClick() {
      if (this.isBtnDisabled) return
      this.getCateParams()
    },
    // 
    showEditPramsDialog(params) {
      this.editParamsForm = params
      this.editParamsVisible = true
    },
    // 
    resetEditParamsForm() {
      this.$refs.editParamsFormRef.resetFields()
      this.getCateParams()
    },
    // 
    editParams() {
      this.$refs.editParamsFormRef.validate(async valid => {
        if (!valid) {
          return
        } else {
          let p = this.editParamsForm
          const { data: result } = await this.$http.put(
            `/categories/${p.cat_id}/attributes/${p.attr_id}`,
            qs.stringify({
              attr_name: p.attr_name,
              attr_sel: p.attr_sel,
              attr_vals: p.attr_vals
            }),
            { header: { 'Content-Type': 'application/x-www-form-urlencoded' } }
          )
          if (result.meta.status !== 200) {
            return this.$message.error('')
          } else {
            this.$message.success('')
            this.getCateParams()
            this.editParamsVisible = false
          }
        }
      })
    },
    // /
    showAddVisible() {
      this.addVisible = true
    },
    // /
    closeAddDialog() {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addAttr() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
          return
        } else {
          const { data: result } = await this.$http.post(
            `/categories/${this.categoriesID}/attributes`,
            qs.stringify({
              attr_name: this.addForm.attr_name,
              attr_sel: this.activeName
            }),
            { header: { 'Content-Type': 'application/x-www-form-urlencoded' } }
          )
          if (result.meta.status !== 201) {
            return this.$message.error(`${this.addParamsText}`)
          } else {
            this.$message.success(`${this.addParamsText}`)
            this.addVisible = false
            this.getCateParams()
          }
        }
      })
    },
    // 
    async delParams(params) {
      const isConfirm = await this.$confirm(
        `${this.addParamsText}, ?`,
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (isConfirm === 'cancel') return
      const { data: result } = await this.$http.delete(
        `/categories/${params.cat_id}/attributes/${params.attr_id}`
      )
      if (result.meta.status !== 200) {
        return this.$message.error(`${this.addParamsText}`)
      } else {
        this.$message.success(`${this.addParamsText}`)
        this.getCateParams()
      }
    },
    //  +new tag input
    showInput(row) {
      row.inputVisible = true
      // $nextTick 
      // console.log(this.$refs.saveTagInput)     undefined
      this.$nextTick(() => {
        //  input  input  disabled  $nextTick  input 
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    //  enter 
    async handleInputConfirm(row) {
      row.inputVisible = false
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        return
      }
      if (typeof row.attr_vals === 'string') {
        row.attr_vals = []
      }
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      const { data: result } = await this.$http.put(
        `/categories/${row.cat_id}/attributes/${row.attr_id}`,
        qs.stringify({
          attr_name: row.attr_name,
          attr_sel: row.attr_sel,
          attr_vals: row.attr_vals.join(' ')
        }),
        { header: { 'Content-Type': 'application/x-www-form-urlencoded' } }
      )
      if (result.meta.status !== 200) {
        return this.$message.error(``)
      } else {
        this.$message.success(``)
      }
    },
    // 
    async handleClose(row) {
      row.attr_vals.splice(row.attr_vals.indexOf(row), 1)
      const { data: result } = await this.$http.put(
        `/categories/${row.cat_id}/attributes/${row.attr_id}`,
        qs.stringify({
          attr_name: row.attr_name,
          attr_sel: row.attr_sel,
          attr_vals: row.attr_vals.join(' ')
        }),
        { header: { 'Content-Type': 'application/x-www-form-urlencoded' } }
      )
      if (result.meta.status !== 200) {
        return this.$message.error(``)
      } else {
        this.$message.success(``)
      }
    }
  },
  computed: {
    // /
    isBtnDisabled() {
      if (this.selectedCate.length === 0) {
        return true
      } else {
        return false
      }
    },
    // id
    categoriesID() {
      if (this.selectedCate.length === 0) {
        return null
      } else {
        return this.selectedCate[this.selectedCate.length - 1]
      }
    },
    // 
    addParamsText() {
      if (this.activeName === 'many') {
        return ''
      } else {
        return ''
      }
    }
  }
}
</script>

<style lang="less" scoped>
.mg-b {
  margin-bottom: 15px;
}
.el-tag {
  margin: 10px;
}
.input-new-tag {
  width: 120px;
}
</style>