<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-row>
        <!--  -->
        <el-col :span="8" class="mg-r">
          <el-input
            placeholder=""
            v-model="queryParams.query"
            class="input-with-select"
            clearable
            @clear="clearSearchText"
          >
            <el-button slot="append" icon="el-icon-search" @click="searchGoods"></el-button>
          </el-input>
        </el-col>
        <!--  -->
        <el-col :span="6">
          <el-button type="primary" @click="goAddGoods"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="goodsListData" border stripe style="width: 100%">
        <el-table-column type="index" label="#">
          <template
            v-slot="slotsProps"
          >{{ (queryParams.pagenum - 1) * queryParams.pagesize + slotsProps.$index + 1}}</template>
        </el-table-column>
        <el-table-column prop="goods_name" label=""></el-table-column>
        <el-table-column prop="goods_price" label="()" width="95px"></el-table-column>
        <el-table-column prop="goods_weight" label="" width="70px"></el-table-column>
        <el-table-column prop="add_time" label="" width="140px">
          <template v-slot="slotsProps">{{slotsProps.row.add_time | dateFormat }}</template>
        </el-table-column>
        <el-table-column label="" width="130px">
          <template v-slot="slotsProps">
            <el-button type="primary" class="el-icon-edit" size="mini"></el-button>
            <el-button
              type="danger"
              class="el-icon-delete"
              size="mini"
              @click="delGoods(slotsProps.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryParams.pagenum"
        :page-sizes="[5, 10, 20]"
        :page-size="queryParams.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      searchText: '',

      // 
      goodsListData: [],

      // 
      queryParams: { query: '', pagenum: 1, pagesize: 5 },

      // 
      total: 0
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    // 
    async getGoodsList() {
      const { data: result } = await this.$http.get('/goods', {
        params: this.queryParams
      })
      if (result.meta.status !== 200)
        return this.$message.error('')
      this.goodsListData = result.data.goods
      this.total = result.data.total
    },

    // 
    searchGoods() {
      this.queryParams.pagenum = 1
      this.getGoodsList()
    },
    // 
    clearSearchText() {
      this.queryParams.pagenum = 1
      this.getGoodsList()
    },
    // 
    goAddGoods() {
      this.$router.push('/add')
    },

    // 
    async delGoods(row) {
      const confirm = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirm === 'cancel') return
      const { data: result } = await this.$http.delete(`/goods/${row.goods_id}`)
      if (result.meta.status !== 200) {
        return this.$message.error('')
      } else {
        this.$message.success('')
        this.getGoodsList()
      }
    },

    // 
    handleSizeChange(ps) {
      this.queryParams.pagesize = ps
      this.getGoodsList()
    },
    // 
    handleCurrentChange(pn) {
      this.queryParams.pagenum = pn
      this.getGoodsList()
    }
  }
}
</script>

<style lang="less" scoped>
.mg-r {
  margin-right: 20px;
  > .input-with-select {
    margin-bottom: 15px;
  }
}
</style>