<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-alert title="" type="info" show-icon center :closable="false"></el-alert>
      <el-steps
        :space="200"
        :active="activeIndex - 0"
        finish-status="success"
        align-center
        class="fs-12"
      >
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <!--  -->
      <el-form
        :model="addGoodsForm"
        :rules="addGoodsFormRules"
        ref="addGoodsFormRef"
        label-width="80px"
        label-position="top"
      >
        <!--  tabs  -->
        <el-tabs
          :tab-position="tabPosition"
          v-model="activeIndex"
          :before-leave="beforeLeaveEvent"
          @tab-click="handleClick"
        >
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addGoodsForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price" required>
              <el-input-number
                v-model="addGoodsForm.goods_price"
                controls-position="right"
                :min="1"
                class="num-left"
              ></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="goods_weight" required>
              <el-input-number
                v-model="addGoodsForm.goods_weight"
                controls-position="right"
                :min="1"
                class="num-left"
              ></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="goods_number" required>
              <el-input-number
                v-model="addGoodsForm.goods_number"
                controls-position="right"
                :min="1"
                class="num-left"
              ></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                v-model="addGoodsForm.goods_cat"
                :options="goodsCateData"
                :props="cascaderProps"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <el-form-item
              :label="item1.attr_name"
              v-for="item1 in this.manyTableData"
              :key="item1.attr_id"
            >
              <el-checkbox-group v-model="item1.attr_vals">
                <el-checkbox v-for="(item2, i) in item1.attr_vals" :key="i" :label="item2" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item
              :label="item.attr_name"
              v-for="item in this.onlyTableData"
              :key="item.attr_id"
            >
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <el-upload
              :action="uploadURL"
              list-type="picture-card"
              :on-preview="handlePictureCardPreview"
              :on-remove="handleRemove"
              :on-success="handleSuccess"
              :headers="headerObj"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <quill-editor v-model="addGoodsForm.goods_introduce"></quill-editor>
            <el-button type="primary" class="btn-mt" @click="addGoods"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <!--  -->
    <el-dialog :visible.sync="showImgVisible">
      <img width="100%" :src="dialogImageUrl" alt />
    </el-dialog>
  </div>
</template>

<script>
import qs from 'qs'
import _ from 'lodash'

export default {
  data() {
    return {
      // 
      activeIndex: '0',
      //  tabs 
      tabPosition: 'left',

      // 
      addGoodsForm: {
        goods_name: '',
        goods_cat: [],
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        goods_introduce: '',
        pics: [],
        attrs: []
      },
      // 
      addGoodsFormRules: {
        goods_name: [
          { required: true, message: '', trriger: 'blur' }
        ],
        goods_price: [
          {
            required: true,
            type: 'number',
            message: '',
            trriger: 'blur'
          }
        ],
        goods_weight: [
          {
            required: true,
            type: 'number',
            message: '',
            trriger: 'blur'
          }
        ],
        goods_number: [
          {
            required: true,
            type: 'number',
            message: '',
            trriger: 'blur'
          }
        ],
        goods_cat: [
          {
            required: true,
            type: 'array',
            message: '',
            trriger: 'blur'
          }
        ]
      },

      // 
      goodsCateData: [],
      // 
      cascaderProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },

      //  input input  string  number  1 itme_vals  Array,  *Invalid prop: type check failed for prop "value". Expected String, Number, got Array.* 
      // 
      manyTableData: [],
      // 
      onlyTableData: [],

      // 
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
      //  upload  ajax  main.js  axios
      headerObj: {
        Authorization: sessionStorage.getItem('token')
      },
      // 
      showImgVisible: false,
      // 
      dialogImageUrl: ''
    }
  },
  created() {
    this.getCategoriesList()
  },
  methods: {
    // 
    async getCategoriesList() {
      const { data: result } = await this.$http.get('/categories')
      if (result.meta.status !== 200) {
        return this.$message.error('')
      }
      this.goodsCateData = result.data
    },

    // 
    beforeLeaveEvent(newActiveIndex, oldActiveIndex) {
      var prohibitSwitchTab
      this.$refs.addGoodsFormRef.validate(valid => {
        if (!valid) {
          prohibitSwitchTab = false
        }
      })
      return prohibitSwitchTab
    },

    //  tabs 
    async handleClick() {
      if (this.activeIndex === '1') {
        const { data: result } = await this.$http.get(
          `/categories/${this.CateID}/attributes`,
          {
            params: { sel: 'many' }
          }
        )
        if (result.meta.status !== 200)
          return this.$message.error('')
        result.data.forEach(item => {
          if (item.attr_vals === '') return
          item.attr_vals = item.attr_vals.split(' ')
        })
        this.manyTableData = result.data
      }
      if (this.activeIndex === '2') {
        const { data: result } = await this.$http.get(
          `/categories/${this.CateID}/attributes`,
          {
            params: { sel: 'only' }
          }
        )
        if (result.meta.status !== 200)
          return this.$message.error('')
        this.onlyTableData = result.data
      }
    },

    // 
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url
      this.showImgVisible = true
    },

    // 
    handleRemove(file) {
      // 1. 
      const filePath = file.response.data.tmp_path
      // 2.  pics  filePath 
      const i = this.addGoodsForm.pics.findIndex(x => x.pic === filePath)
      // 3.  pics 
      this.addGoodsForm.pics.splice(i, 1)
    },

    // 
    handleSuccess(response) {
      //  push  pics 
      const picInfo = { pic: response.data.tmp_path }
      this.addGoodsForm.pics.push(picInfo)
    },

    // 
    async addGoods() {
      const form = _.cloneDeep(this.addGoodsForm)
      form.goods_cat = form.goods_cat.join(',')
      // 
      this.manyTableData.forEach(item => {
        if (typeof item.attr_vals === 'object') {
          item.attr_vals = item.attr_vals.join(' ')
        }
        const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals }
        this.addGoodsForm.attrs.push(newInfo)
      })
      // 
      this.onlyTableData.forEach(item => {
        if (typeof item.attr_vals === 'object') {
          item.attr_vals = item.attr_vals.join(' ')
        }
        const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals }
        this.addGoodsForm.attrs.push(newInfo)
      })
      form.attrs = this.addGoodsForm.attrs
      //  post 
      const { data: result } = await this.$http.post(
        '/goods',
        qs.stringify(form),
        {
          header: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        }
      )
      if (result.meta.status !== 201)
        return this.$message.error('')
      this.$message.success('')
      this.$router.push('/goods')
    }
  },
  computed: {
    CateID() {
      return this.addGoodsForm.goods_cat[this.addGoodsForm.goods_cat.length - 1]
    }
  }
}
</script>

<style lang="less" scoped>
.el-alert {
  margin-bottom: 15px;
}
.el-steps {
  margin-bottom: 10px;
}
.el-input-number,
.el-cascader {
  width: 100%;
}
.el-checkbox {
  margin: 0 10px 10px 0 !important;
}

.btn-mt {
  margin-top: 15px;
}
</style>